<script>
import axios from 'axios'
export default {
	data() {
		return {
			form: {
				project_name: '',
				sequence: '',
				file: null
			},
			fileName: '',
			top_sequences: [
				{
					sequence: 'GTGACATTTTGAATTTGAAGTATTATAATGTGTGG',
					strength: '253.547558',
					score: '100'
				}
			],
			isSubmit: false
		}
	},
	methods: {
		onsubmit() {
        const data = new FormData();

        // Add project_name
        data.append('project_name', this.form.project_name);

        // Conditionally add sequence and file
        if (this.form.sequence) {
            data.append('sequence', this.form.sequence);
        }

        if (this.form.file) {
            data.append('file', this.form.file);
        }

        // Ensure at least one input is provided
        if (!this.form.sequence && !this.form.file) {
            alert('Please provide a sequence or a file.');
            return;
        }

        // Submit the form
        axios.post('http://139.129.26.28:81/submit', data)
            .then(response => {
                this.isSubmit = true;
                console.log('Submission successful:', response);
				alert('Submit successfully, please wait a few seconds!');
            })
            .catch(error => {
                console.error('Submission failed:', error.response || error.message);
                alert('Submission failed, please check your input.');
            });
		},
		uploadFile(event) {
			console.log(event.target.files);
			this.form.file = event.target.files[0];
			console.log('Selected file:', this.form.file);
		},
		downloadResult(file) {
			if (!this.isSubmit) {
				alert('Please submit the form first.');
				return false;
			}
			const link = document.createElement('a');
			link.href = `http://139.129.26.28:81/download/${file}`;
			link.download = this.fileName;
			link.click();
		},
		toPage() {
			console.log('toPage');
			this.$router.push('/De novo Promoter Generation');
		}
	}
}
</script>

